*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
@media only screen and (min-width: 56.25em) {
  html {
    font-size: 59%;
  }
}
@media only screen and (min-width: 75em) {
  html {
    font-size: 60%;
  }
}

a,
a:hover,
a:active,
a:focus {
  text-decoration: none;
  outline: none;
}

ul,
li {
  list-style: none;
}

input,
textarea {
  outline: none;
}

body {
  box-sizing: border-box;
  position: relative;
  font-size: 1.6rem;
}

.wrapper {
  max-width: 100%;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

section {
  background-color: #fafafa;
  position: relative;
}

body {
  color: #333;
  line-height: 1.8;
}

a {
  color: teal;
  transition: all 0.3s;
}
a:hover {
  color: rgba(0, 128, 128, 0.7);
}

.container {
  width: 100%;
  margin: auto;
}
@media only screen and (min-width: 56.25em) {
  .container {
    width: 95%;
  }
}
@media only screen and (min-width: 75em) {
  .container {
    width: 120rem;
  }
}

.u-flex {
  display: flex;
  flex-direction: column;
}
@media only screen and (min-width: 75em) {
  .u-flex {
    flex-direction: row;
  }
}
.u-flex main {
  flex-grow: 1;
}

.u-block {
  background: rgba(217, 217, 217, 0.5);
  padding: 2rem;
  border-radius: 2rem;
  width: 100%;
}
.u-block:first-child {
  margin-bottom: 3rem;
}

.my-6 {
  margin: 6rem 0;
}

.u-phone {
  background: rgba(217, 217, 217, 0.5);
  border-radius: 2rem;
  font-size: 2rem;
  padding: 1rem 0;
}

.button {
  padding: 1rem 2rem;
  background: rgba(0, 128, 128, 0.8);
  color: #fff;
  border-radius: 2rem;
  margin: 2rem auto;
  display: inline-block;
}

@media only screen and (min-width: 75em) {
  .sidebar {
    margin-left: 2rem;
    max-width: 33rem;
  }
}
.sidebar__title {
  padding-bottom: 1rem;
  margin-bottom: 2rem;
  border-bottom: 1px solid #888;
}

.header {
  background: #555;
  padding: 3rem 0;
  border-bottom: 3px solid #888;
}
.header__title {
  color: teal;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media only screen and (min-width: 56.25em) {
  .header__title {
    flex-direction: row;
  }
}
@media only screen and (min-width: 56.25em) {
  .header__link {
    margin-right: 2rem;
  }
}
.header small {
  color: #d9d9d9;
  font-size: 1.3rem;
  margin: 2rem;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
@media only screen and (min-width: 56.25em) {
  .header__wrapper {
    flex-direction: row;
  }
}
.header__input {
  width: 25rem;
  padding: 1rem;
  border-radius: 2rem;
}

.nav {
  height: 5rem;
  width: 100%;
  background-color: #555;
}
.nav__list {
  display: flex;
  height: 5rem;
  line-height: 5rem;
}
.nav__link {
  color: #fff;
  padding: 0 2rem;
  height: 100%;
  width: 100%;
  display: block;
  transition: background 0.3s;
}
.nav__link:hover {
  background: teal;
  color: #fff;
}

.footer {
  background: #555;
  padding: 3rem 0;
}
.footer__container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}
@media only screen and (min-width: 37.5em) {
  .footer__container {
    flex-direction: row;
  }
}
.footer__nav-list {
  display: flex;
}
.footer__nav-link {
  padding: 1rem 2rem;
  display: inline-block;
  color: #fff;
}
.footer__copy {
  padding: 1rem 2rem;
  color: #fff;
}

.post {
  max-width: 100%;
  margin-bottom: 6rem;
  padding-bottom: 4rem;
  border-bottom: 1px solid rgba(0, 128, 128, 0.8);
}
.post__title {
  margin-bottom: 2rem;
}
.post__meta {
  margin-bottom: 2rem;
  background: rgba(0, 128, 128, 0.8);
  color: #fff;
  border-radius: 2rem;
  padding: 1rem 2rem;
}
.post__meta a {
  color: #fff;
}
.post__meta a:hover {
  color: rgba(253, 208, 0, 0.8);
}
.post__p {
  margin-bottom: 1rem;
}
.post__thumbnail {
  height: auto;
  max-width: 40rem;
  margin: auto;
}
.post__thumbnail > img {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}
.post__content-aside {
  background: rgba(253, 208, 0, 0.8);
  padding: 2rem 1rem;
  border-radius: 2rem;
}
.post__content-aside small {
  font-weight: bold;
}
.post__content-link {
  background: rgba(217, 217, 217, 0.8);
  padding: 2rem 1rem;
  border-radius: 2rem;
}
.post__content-gallery {
  background: rgba(199, 199, 199, 0.8);
  padding: 2rem 1rem;
  border-radius: 2rem;
}
.post__content-gallery img {
  width: 100%;
  height: auto;
}

.page__wrapper {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
@media only screen and (min-width: 56.25em) {
  .page__wrapper {
    flex-direction: row;
  }
}
.page__wrapper .page_item {
  padding: 1rem;
}
@media only screen and (min-width: 56.25em) {
  .page__wrapper .page_item {
    margin-left: 1rem;
  }
}
.page__wrapper .page_item.current_page_item {
  font-weight: bold;
}
@media only screen and (min-width: 56.25em) {
  .page__nav {
    order: 2;
  }
}
.page__nav-list {
  display: flex;
  justify-content: flex-end;
}
@media only screen and (min-width: 56.25em) {
  .page__nav-list {
    justify-content: space-between;
  }
}
.page__nav-parent-item {
  padding: 1rem 2rem;
  border-right: 2px solid teal;
}
.page__nav-parent-link {
  color: #000;
  font-weight: bold;
}

.archive__container {
  padding: 2rem 1rem;
  border-bottom: 1px solid rgba(0, 128, 128, 0.8);
}
.archive__title {
  padding: 2rem 0;
}
.archive__p {
  padding: 0 2rem;
}